/* Settings Popup Container */

.settings-popup{
    color:white;
    z-index:50;
}

/* Settings Pop-up Header */

.settings-popup-header{
    flex-grow: 10;
}

/* Settings Pop-up Footer */

.popup-btn{
    align-self: center;
    color: white;
    height:75%;
    width: 20%;
    margin-right:5px;
    background: rgba(255,255,255,0);
    border-width: 0px;
    
}

.popup-btn:hover{
    background: rgba(255,255,255,0.1);
    animation: light-up 0.25s;
}

.popup-btn:focus{
    outline:none;
}

@keyframes light-up{
    0%{
        background: rgba(255,255,255,0);
    }

    100%{
        background: rgba(255,255,255,0.1)
    }
}