* {
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.nav-icon{
  height:50px;
}

.center{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 25%;
    left: 0%;
    right: 0%;
    text-align: center;
}

.pointer{
  cursor:pointer;
}

.h-wo-nav{
  height: calc(100vh - 56px);
}

