/* slider */

.checkbox {
	opacity: 0;
	position: absolute;
}

.label {
    background-color: #FFF;
    border: 1px solid#c7c1c1;
	border-radius: 50px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 5px;
	position: relative;
    height: 18px;
    width: 39px;
	transform: scale(1.5);
}

.label .ball {
	background-color: #c7c1c1;
	border-radius: 50%;
	position: absolute;
    top: 1px;
    left: 2px;
    height: 14px;
    width: 14px;
	transform: translateX(0px);
	transition: transform 0.2s linear;
}

.checkbox:checked + .label .ball {
	transform: translateX(19px);
}


.fa-moon {
    transform: translateX(-2.5px) translateY(-0.5px);
    font-size:13px;
	color: #ebd993;
}

.fa-sun {
    transform: translateX(3px);
    font-size: 14px;
	color: #f39c12;
}